import { useState } from 'react';
import { configureStore, createSlice, PayloadAction } from '@reduxjs/toolkit'
import { IfMaybeUndefined } from '@reduxjs/toolkit/dist/tsHelpers';

interface UserState {
  name: string;
  age: number;
}


let user = createSlice({
    name : 'user',
    initialState : { name : 'kim', age : 20 } as UserState,
    reducers :{
      changeName(state){
        state.name = 'park'
      },

    //state     action 
      increase(state, action: PayloadAction<number>){
        state.age += action.payload
      }
    }
  });

  
//   increase(10)
//   increase(100)

export const { changeName, increase } = user.actions;

interface CartItem {
  id?: number;
  imgurl?: string;
  name: string ;
  count: number;
}

let cart = createSlice({
    name : 'cart',
    initialState : [
      
    ]as CartItem[],
    reducers : {
     addCount(state, action: PayloadAction<number>) {
        let num = state.findIndex((a) => {
          return a.id === action.payload;
      });
      console.log(num);
      state[num].count++;
    },
    decreaseCount(state, action: PayloadAction<number>) {
      let num = state.findIndex((a) => {
        return a.id === action.payload;
      });
      console.log(num);
      if (state[num].count > 0) {
        state[num].count--;
      } else if (state[num].count === 0) {
        alert("   .");
      }
    },
    addItem(state, action: PayloadAction<CartItem>) {
      let num = state.findIndex((a) => a.id === action.payload.id);
      if (num !== -1) {
        state[num].count++;
      } else {
        state.push(action.payload);
      }
    },

    deleteItem(state, action: PayloadAction<number>) {
      let num = state.findIndex((a) => {
        return a.id === action.payload;
      });
      state.splice(num, 1);
    },
    sortName(state) {
      state.sort((a, b) => (a.name > b.name ? 1 : -1));
    }
    }
  });


// addCount(1)


export const { addCount, decreaseCount, addItem, deleteItem, sortName } = cart.actions;

export default configureStore({
    reducer: {
      user: user.reducer,
      cart: cart.reducer,
    },
  });
  