
// Import Swiper React components
import { Swiper, SwiperSlide } from "swiper/react";

// Import Swiper styles
import "swiper/css";
import "swiper/css/pagination";
import "swiper/css/navigation";
// import required modules
import { Pagination, Navigation, Autoplay } from "swiper";

import "./slider.css";
import slide_data from "./slide_data"

// type import 
import {Slide_data} from "./slide_data";

interface SlideProps {
    a : Slide_data;
}
function Slider():JSX.Element {
    return (
        <>
            <Swiper
                slidesPerView={1}
                loop={true}
                autoplay={{
                    delay: 2500,
                    disableOnInteraction: false,
                }}
                navigation={true}
                modules={[Pagination, Navigation, Autoplay]}
                className="mySwiper"
            >
                {
                    slide_data.map((a:Slide_data, i:number) => {
                        return (
                            <SwiperSlide key={i}>
                                <Sliderbox a={a} />
                            </SwiperSlide>
                        )
                    })
                }
            </Swiper>
        </>
    );
}

function Sliderbox(props:SlideProps):JSX.Element {
    return (
        <>
            <div>
                <img src={props.a.imgurl} alt=""/>
            </div>
            <div className="slide_txt">
                <h3>{props.a.subtitle}</h3>
                <h1>{props.a.title}</h1>
                <p>{props.a.content}</p>
            </div>
        </>
    )
}

export default Slider;
