import { Table } from "react-bootstrap";
import { useDispatch, useSelector } from "react-redux";
// import store, {changeName, increase} from './store.js'
import { addCount, decreaseCount, deleteItem, sortName } from "./store";
import { Button } from "react-bootstrap";
import { Link } from "react-router-dom";


interface RootState {
  user: {
    name: string;
    age: number;
  };
  cart: {
    id: number;
    imgurl: string;
    name: string;
    count: number;
  }[];
}

function Cart(): JSX.Element {
  let state = useSelector((state: RootState) => state);
  // console.log(state.cart[0].name);

  // dispatch  store.js   
  let dispatch = useDispatch();

  const smallProdcuctStyle: React.CSSProperties = {
    border: "1px solid #ddd",
    width: "100px",
    height: "80px",
    cursor: "pointer",
  };

  let textverticalAlign: React.CSSProperties = {
    verticalAlign: "middle",
    textAlign: "center",
  };

  return (
    <>
      <h5 style={{ padding: "50px" }}>
        {state.user.name} {state.user.age} 
      </h5>
      {/* <button onClick={()=>{ dispatch(increase())}}>+</button>
       <button onClick={()=>{ dispatch(increase(100))}}>+</button> */}
      <Table style={{ width: "1260px", margin: "0 auto" }}>
        <thead>
          <tr>
            <th>id</th>
            <th></th>
            <th></th>
            <th></th>
            <th></th>
          </tr>
        </thead>
        <tbody>
          {state.cart.map((a, i:number) => (
            <tr key={i}>
              <td style={textverticalAlign}>{state.cart[i].id}</td>

              <td>
                <Link to={`/detail/${state.cart[i].id}`}>
                  <img
                    src={`img/${state.cart[i].imgurl}`}
                    style={smallProdcuctStyle}
                  />
                </Link>
              </td>
              <td style={textverticalAlign}>{state.cart[i].name}</td>
              <td style={textverticalAlign}>{state.cart[i].count}</td>
              <td style={textverticalAlign}>
                <Button
                  onClick={() => {
                    dispatch(addCount(state.cart[i].id));
                  }}
                  variant="outline-success"
                  style={{ marginRight: "10px" }}
                >
                  +
                </Button>

                <Button
                  onClick={() => {
                    dispatch(decreaseCount(state.cart[i].id));
                  }}
                  variant="outline-warning"
                  style={{ marginRight: "10px" }}
                >
                  -
                </Button>

                <Button
                  onClick={() => {
                    dispatch(deleteItem(state.cart[i].id));
                  }}
                  variant="outline-danger"
                >
                  
                </Button>
              </td>
            </tr>
          ))}
        </tbody>
      </Table>
      <Button
        variant="outline-primary"
        onClick={() => {
          dispatch(sortName());
        }}
      >
        
      </Button>{" "}
    </>
  );
}

export default Cart;
