import Tabs from '@mui/material/Tabs';
import Tab from '@mui/material/Tab';
import Typography from '@mui/material/Typography';
import Box from '@mui/material/Box';
import "./best.css"

// Import Swiper styles
import React, { useRef, useState, ChangeEvent } from "react";
import { Swiper, SwiperSlide } from "swiper/react";
import { Link } from 'react-router-dom'

// import required modules
import { Pagination, Navigation } from "swiper";

// import data
import best_total_data from "./best_total_data"
import best_female_data from "./best_female_data"
import best_male_data from "./best_male_data"
import best_shoes_data from "./best_shoes_data"

// import type
import {Best_total_data} from "./best_total_data"
import {Best_female_data} from "./best_female_data"
import {Best_male_data} from "./best_male_data"
import {Best_shoes_data} from "./best_shoes_data"

interface BestProps1 {
  data1: Best_total_data[];
  res12: number[];
}
interface BestProps2 {
  data2: Best_female_data[];
  res13: number[];
}
interface BestProps3 {
  data3: Best_male_data[];
  res14: number[];
}
interface BestProps4 {
  data4: Best_shoes_data[];
  res15: number[];
}
interface TabPanelProps {
  children: React.ReactNode;
  value:number;
  index:number;
  style: {
    height: string;
}
}

function Best(): JSX.Element {
  const [value, setValue] = useState<number>(0);

  const handleChange = (event:ChangeEvent<{}>, newValue: number) => {
    setValue(newValue);
  };

  let [data1] = useState<Best_total_data[]>(best_total_data);
  let [data2] = useState<Best_female_data[]>(best_female_data);
  let [data3] = useState<Best_male_data[]>(best_male_data);
  let [data4] = useState<Best_shoes_data[]>(best_shoes_data);

  let [res12] = useState<number[]>([56, 57, 58, 59, 60, 61, 62 ,63]);
  let [res13] = useState<number[]>([64, 65, 66, 67, 68, 69, 70, 71]);
  let [res14] = useState<number[]>([72, 73, 74, 75, 76, 77, 78, 79]);
  let [res15] = useState<number[]>([80, 81, 82, 83, 84, 85, 86]);


  return (
    <Box sx={{ width: '100%' }}>
      <Box sx={{ borderBottom: 1, borderColor: 'divider' }}>
        <Tabs value={value} onChange={handleChange} aria-label="basic tabs example">
          <Tab label="" {...a11yProps(0)} />
          <Tab label="" {...a11yProps(1)} />
          <Tab label="" {...a11yProps(2)} />
          <Tab label="/" {...a11yProps(3)} />
        </Tabs>
      </Box>
      <TabPanel value={value} index={0} style={{ height: '1000px' }}>
        <Swiper

          className="mySwiper4"
        >
          <SwiperSlide>
            <All_box1 data1={data1} res12={res12}/>
          </SwiperSlide>


        </Swiper>
      </TabPanel >
      <TabPanel value={value} index={1} style={{ height: '1000px' }}>
        <Swiper

          className="mySwiper4"
        >
          <SwiperSlide>
            <All_box2 data2={data2} res13={res13}/>
          </SwiperSlide>

        </Swiper>
      </TabPanel>
      <TabPanel value={value} index={2} style={{ height: '1000px' }}>
        <Swiper

          className="mySwiper4"
        >
          <SwiperSlide>
            <All_box3 data3={data3} res14={res14}/>
          </SwiperSlide>
        </Swiper>
      </TabPanel>
      <TabPanel value={value} index={3} style={{ height: '1000px' }}>
        <Swiper

          className="mySwiper4"
        >
          <SwiperSlide>
            <All_box4 data4={data4} res15={res15}/>
          </SwiperSlide>
        </Swiper>
      </TabPanel>

    </Box>
  );
}

function All_box1(props:BestProps1): JSX.Element {
  return (
    <>
      {
        props.data1.map((a:Best_total_data, i:number) => {
          return (
            <div className="box_wrap" style={{ marginRight: '15px' }}>
               <Link to={`/detail/${props.res12[i]}`}>
              <img src={a.imgurl} style={{ width: '100%' }} />
              <div className="text_box">
                <p>{a.title}</p>
                <p>{a.content}</p>
                <span className="percent2">{a.sale}</span><span className="original2">{a.original}</span><span className="price2">{a.price}</span>
              </div>
              </Link>
            </div>
          )
        })
      }
    </>
  )
}

function All_box2(props:BestProps2): JSX.Element {
  return (
    <>
      {
        props.data2.map((a:Best_female_data, i:number) => {
          return (
            <div className="box_wrap" style={{ marginRight: '15px' }}>
              <Link to={`/detail/${props.res13[i]}`}>
              <img src={a.imgurl} style={{ width: '100%' }} />
              <div className="text_box">
                <p>{a.title}</p>
                <p>{a.content}</p>
                <span className="percent2">{a.sale}</span><span className="original2">{a.original}</span><span className="price2">{a.price}</span>
              </div>
              </Link>
            </div>
          )
        })
      }
    </>
  )
}


function All_box3(props:BestProps3): JSX.Element {
  return (
    <>
      {
        props.data3.map((a:Best_male_data, i:number) => {
          return (
            <div className="box_wrap" style={{ marginRight: '15px' }}>
              <Link to={`/detail/${props.res14[i]}`}>
              <img src={a.imgurl} style={{ width: '100%' }} />
              <div className="text_box">
                <p>{a.title}</p>
                <p>{a.content}</p>
                <span className="percent2">{a.sale}</span><span className="original2">{a.original}</span><span className="price2">{a.price}</span>
              </div>
              </Link>
            </div>
          )
        })
      }
    </>
  )
}

function All_box4(props:BestProps4): JSX.Element {
  return (
    <>
      {
        props.data4.map((a:Best_shoes_data, i:number) => {
          return (
            <div className="box_wrap" style={{ marginRight: '15px' }}>
              <Link to={`/detail/${props.res15[i]}`}>
              <img src={a.imgurl} style={{ width: '100%' }} />
              <div className="text_box">
                <p>{a.title}</p>
                <p>{a.content}</p>
                <span className="percent2">{a.sale}</span><span className="original2">{a.original}</span><span className="price2">{a.price}</span>
              </div>
              </Link>
            </div>
          )
        })
      }
    </>
  )
}



function a11yProps(index: number): { id: string; "aria-controls": string }{
  return {
    id: `simple-tab-${index}`,
    'aria-controls': `simple-tabpanel-${index}`,
  };
}

function TabPanel(props:TabPanelProps):JSX.Element {
  const { children, value, index, ...other } = props;

  return (
    <div
      role="tabpanel"
      hidden={value !== index}
      id={`simple-tabpanel-${index}`}
      aria-labelledby={`simple-tab-${index}`}
      {...other}
    >
      {value === index && (
        <Box sx={{ p: 3 }}>
          <Typography>{children}</Typography>
        </Box>
      )}
    </div>
  );
}

export default Best;