import React, { useRef, useState ,} from "react";
// Import Swiper React components
import { Swiper, SwiperSlide } from "swiper/react";
import {Link } from 'react-router-dom'

// Import Swiper styles
import "swiper/css";
import "swiper/css/pagination";
import "swiper/css/navigation";
import "./focus.css";
import focus_data1 from "./focus_data1";
import focus_data1_1 from "./focus_data1_1";
import focus_data2 from "./focus_data2";
import focus_data2_1 from "./focus_data2_1";
import focus_data3 from "./focus_data3";
import focus_data3_1 from "./focus_data3_1";



// import required modules
import { Autoplay, Pagination, Navigation } from "swiper";

// type import 
import {Focus_data1} from "./focus_data1";
import {Focus_data1_1} from "./focus_data1_1";
import {Focus_data2} from "./focus_data2";
import {Focus_data2_1} from "./focus_data2_1";
import {Focus_data3} from "./focus_data3";
import {Focus_data3_1} from "./focus_data3_1";

interface BfocusProps1 {
    bfdata1 : Focus_data1[];
    bfdata2 : Focus_data1_1[];
    res9 : number[]
    
}

interface BfocusProps2 {
    bfdata3 : Focus_data2[];
    bfdata4 : Focus_data2_1[];
    res10 : number[]
}

interface BfocusProps3 {
    bfdata5: Focus_data3[];
    bfdata6 : Focus_data3_1[];
    res11 : number[]
}



function Focus(): JSX.Element {
    const [bfdata1] = useState<Focus_data1[]>(focus_data1);
    const [bfdata2] = useState<Focus_data1_1[]>(focus_data1_1);
    const [bfdata3] = useState<Focus_data2[]>(focus_data2);
    const [bfdata4] = useState<Focus_data2_1[]>(focus_data2_1);
    const [bfdata5] = useState<Focus_data3[]>(focus_data3);
    const [bfdata6] = useState<Focus_data3_1[]>(focus_data3_1);

    let [res9] = useState<number[]>([38, 39, 40, 41, 42, 43]);
    let [res10] = useState<number[]>([44, 45, 46, 47, 48, 49]);
    let [res11] = useState<number[]>([50, 51, 52, 53, 54, 55]);
    
    return (
        <>
            <Swiper
                spaceBetween={30}
                centeredSlides={true}
                autoplay={{
                    delay: 2500,
                    disableOnInteraction: false,
                }}
                pagination={{
                    clickable: true,
                }}
                navigation={true}
                modules={[Autoplay, Pagination, Navigation]}
                className="mySwiper3"
            >
                <SwiperSlide>
                    <Bfocusbox1 bfdata1={bfdata1} bfdata2={bfdata2}  res9={res9}/>
                </SwiperSlide>
                <SwiperSlide>
                    <Bfocusbox2 bfdata3={bfdata3} bfdata4={bfdata4} res10={res10}/>
                </SwiperSlide>
                <SwiperSlide>
                    <Bfocusbox3 bfdata5={bfdata5} bfdata6={bfdata6} res11={res11}/>
                </SwiperSlide>
            </Swiper>
        </>
    );
}



function Bfocusbox1(props:BfocusProps1):JSX.Element {
    return (
        <>
        
            {
                props.bfdata1.map((a:Focus_data1, i:number) => {
                    return (
                        <div className="bfBox_top">
                            <img src={a.imgurl} />
                        </div>
                    )
                })
            }
            
            {
                props.bfdata2.map((a:Focus_data1_1, i:number) => {
                    return (
                        
                        <div className="bfBox_bottom">
                            <Link to={`/detail/${props.res9[i]}`}>
                            <img src={a.imgurl} />
                            <div className="bfBox_txt">
                                <h6>{a.title}</h6>
                                <p>{a.content}</p>
                                <span>{a.sale}</span><span>{a.price}</span>
                            </div>
                            </Link>
                        </div>
                        
                    )
                })
            }
            
        </>
    )
}


function Bfocusbox2(props:BfocusProps2):JSX.Element{
    return (
        <>
            {
                props.bfdata3.map((a:Focus_data2, i:number) => {
                    return (
                        <div className="bfBox_top">
                            <img src={a.imgurl} />
                        </div>
                    )
                })
            }
            
            {
                props.bfdata4.map((a:Focus_data2_1, i:number) => {
                    return (
                        
                        <div className="bfBox_bottom">
                            <Link to={`/detail/${props.res10[i]}`}>
                            <img src={a.imgurl} />
                            <div className="bfBox_txt">
                                <h6>{a.title}</h6>
                                <p>{a.content}</p>
                                <span>{a.sale}</span><span>{a.price}</span>
                            </div>
                            </Link>
                        </div>
                        
                    )
                })
            }
            
        </>
    )
}


function Bfocusbox3(props:BfocusProps3):JSX.Element{
    return (
        <>
            {
                props.bfdata5.map((a:Focus_data3, i:number) => {
                    return (
                        <div className="bfBox_top">
                            <img src={a.imgurl} />
                        </div>
                    )
                })
            }
            
            {
                props.bfdata6.map((a:Focus_data3_1, i:number) => {
                    return (
                        
                        <div className="bfBox_bottom">
                            <Link to={`/detail/${props.res11[i]}`}>
                            <img src={a.imgurl} />
                            <div className="bfBox_txt">
                                <h6>{a.title}</h6>
                                <p>{a.content}</p>
                                <span>{a.sale}</span><span>{a.price}</span>
                            </div>
                            </Link>
                        </div>
                        
                    )
                })
            }
            
        </>
    )
}

export default Focus;